/*------------------------------------*\
 Components > Logo
\*------------------------------------*/

.c-logo {
 @include mq($from: tablet) {
 margin: 0 auto;
 margin-top: -30px;
 }

 h1 {
 margin-bottom: 0;
 }

 &__link {
 display: block;
 line-height: 1;
 font-size: double($font-size-tiny);
 transition: color $global-transition ease-in-out;

 @include mq($from: tablet) {
 font-size: double($font-size-base);
 }

 @include attention() {
 color: $color-brand;
 }
 }

 &__img {
 max-height: 120px;
 }
}